
label {
  color: $concrete;
  font-size: 15px;
  display: block;
  margin: 0;
  font-weight: normal;
  line-height: 1.8;
}

[type='text'], [type='password'], [type='date'], [type='datetime'], [type='datetime-local'], [type='month'], [type='week'], [type='email'], [type='number'], [type='search'], [type='tel'], [type='time'], [type='url'], [type='color'], textarea {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 2.4375rem;
  padding: 0.5rem;
  border: 1px solid $calm;
  margin: 0 0 0.5rem;
  font-family: inherit;
  font-size: 1rem;
  color: #0a0a0a;
  background-color: #fefefe;
  box-shadow: 0 1px 2px rgba(10, 10, 10, 0.1);
  border-radius: 0.3rem;
  -webkit-transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
  transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;

  &:hover {
    border: 1px solid $concrete;
    -webkit-transition: border-color 0.25s ease-in-out;
    transition: border-color 0.25s ease-in-out;
  }

  &:focus {
    border: 1px solid $calm;
    background-color: $iced;
    outline: none;
    box-shadow: 0 0 5px #cacaca;
    -webkit-transition: box-shadow 0.5s, border-color 0.25s ease-in-out, background-color 0.25s ease-in-out;
    transition: box-shadow 0.5s, border-color 0.25s ease-in-out, background-color 0.25s ease-in-out;
  }

  &[disabled] {
    border: 1px solid $slate;
    background-color: transparent;
  }

  .has-error & {
    border-color: $blazed;
  }
}

.has-error {

  .help-block {
    color: $blazed;
    font-style: italic;
  }

}

input + input, label + label {
  margin-top: 1rem;
}

input[type="checkbox"]{

  display:none;

  + label:after {
    content: "";
    display:inline-block;
    width:19px;
    height:19px;
    margin:-1px 4px 0 0;
    vertical-align:middle;
    background: $white;
    cursor:pointer;
    border: 1px solid $breeze;
    border-radius: 3px;
    margin-left: 10px;
  }

  + label:hover:after {
    background-color: $iced;
  }

  &:checked + label:after {
    content: "\2713";
  }

}

.styled-select select {
  background: transparent;
  border-radius: 3px;
  -webkit-appearance: none;
  border: 1px solid $calm;
  box-shadow: 0 1px 2px rgba(10, 10, 10, 0.1);
}

